.hero{
    display: flex;
}
.hero__content, .hero__img{
    flex: 0 0 50%;
}
.hero__content{
    margin-top: 0rem;
}
.hero__title{
    font-size: 3.6rem;
    font-weight: 700;
    line-height: 4rem;
}
.hero__description{
    font-size: 1.45rem;
    font-weight: 500;
    color: var(--color-gray);
    margin: 2.5rem 0rem;
    line-height: 2.2rem;
}
.hero__img{
    margin-top: 3.2rem;
    margin-left: 2.5rem;
    transform: scale(1.45);
    z-index: -50;
}
@media screen and (max-width: 768px) {
    .hero{
        flex-direction: column;
        margin: 8rem 0rem;
    }
    .hero__content{
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 550px;
        width: 90%;
    }
    .hero__title{
        text-align: center;
        font-size: 40px;
    }
    .hero__description{
        text-align: center;
        margin-bottom: 1.2rem;
    }
    .hero__img{
        transform: scale(1.3);
        margin: 8rem 0rem 0rem 0rem;
    }
}