/* Header */
.header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 2.6rem;
}
.header__brand{
  margin-left: 1rem;
}
.header__menu{
  width: 20px;
  height: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  display: none;
}
.header__menu span{
  height: 2px;
  display: block;
  background-color: var(--color-gray);
  border-radius: 5px;
}
.header__navbar {
  flex: 0 0 75%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar {
  display: flex;
}
.navbar__item {
  margin-right: 1.5rem;
}
.navbar__item:last-of-type {
  margin-right: 0rem;
}
.navbar__link {
  display: inline-block;
  font-size: .97rem;
  padding: 0.65rem 1rem;
  font-weight: 600;
  color: var(--color-dark);
  transition: all .4s ease-in-out;
}
.navbar__link:hover{
  color: var(--color-primary);
}
.navbar__link--active {
  color: var(--color-primary);
}

@media screen and (max-width: 768px) {
  .header{
    padding: 0px 1.5rem;
  }
  .header__menu{
    display: flex;
  }
  .header__navbar{
    width: 100%;
    display: none;
  }
}